html {
  background: #fff;
  min-height: 100%;
  padding: 0px;
}
 

body {
  background: #fff;
  min-height: 100%;
  font-size: large;
  padding: 0px;
  margin: 0px;
  text-align:center;
}

.domain {
  background: #eee;
  padding: 4em;
}

.ddl_superSelect {
  max-width: 600px;
  margin: 0px auto;
  text-align:left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 100;
  text-align:center;
}

h2 {
  font-size: 100px;
  padding-top: 0.5em;
  border-top: solid 1px #bbb;
  margin-top: 0.7em;
  margin-bottom: 0px;
}


.helpButton {
  font-size:xx-large;
  -webkit-transform: rotate(5deg);
      -ms-transform: rotate(5deg);
          transform: rotate(5deg);
  margin-left: 50px;
}


.appButton {
  cursor:pointer;
  padding: 0.3rem 1rem;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
  border: solid 2px #cc9;
  background: #ffc;
  color: #996;
  font-size:xx-large;
}

.appButton:hover,
.appButton:focus {
  background: #eea;
  color: #663;
  border: solid 2px #996;

}

.closeHelpButton {
  -webkit-transform: rotate(-5deg);
      -ms-transform: rotate(-5deg);
          transform: rotate(-5deg);
  display: block;
  margin: 2rem auto;
}


.help-text {
  text-align:left;
  line-height: 2;
}

h3 {
  font-size: 60px;
  margin-bottom: 20px;
}

.ddl_domain {
  text-decoration: underline;
  font-weight: bold;
  margin-right: 10px;
}

.ddl_categories {
  clear:left;
  display: block;
}

.ddl_category {
  border: solid 1px #ccc;
  font-size:smaller;
  padding: 3px 6px;
  border-radius: 3px;
  color: #666;
  
  margin-right: 10px;
  font-weight: normal;
  white-space: nowrap;
}

.ddl_item {
  padding: 5px 20px;
  line-height: 2;
}

.close_Metadata {
 
}

.appButton--wide {
  display: block;
}

.metadata-content {
  
  font-size: x-large;
  font-family: monospace;
  text-align:left;
  line-height: 1.7;
  background: #eee;

}

.appButton--halfwide {
  display: inline-block;
  width: 40%;
  font-size: xx-large;
}

.appButton--link {
  text-decoration :none;
  color: #333;
  
}

.ReactModal__Overlay {
  background: red;
}

.appButton--cornerClose {
  position: absolute;
  right: 1rem;
  top: 1rem;

}


