.choice {
    display: inline-block;
    width: 100%;
    max-width: 600px;
    clear:left;
}

.sortBy {
    display: inline-block;
    background: #fff;
    color: #666;
    padding: 0.5em;
    border: none;
}

.sortBy--selected {
    border: solid 1px #ddd;
    border-bottom: none;
    font-weight: bold;
    color: #000;
}


